import { useEffect, useState } from 'react';
import CarDetails from '../components/maintenanceEdit/CarDetails';
import Heading from '../components/maintenanceEdit/Heading';
import MaintenanceOrderForm from '../forms/maintenanceOrder/MaintenanceOrderForm';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useParams } from 'react-router-dom';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { MaintenanceOrder } from '@/types/maintenance';

const MaintenanceEditPage = () => {
    const params = useParams();
    const { id } = params;
    const [maintenanceOrder, setMaintenanceOrder] = useState<MaintenanceOrder | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchMaintenanceOrder = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.maintenance.show + id,
                });
                setMaintenanceOrder(response.data.data);
            } catch (error) {
                setError('Failed to load maintenanceOrder details');
            } finally {
                setLoading(false);
            }
        };

        if (id) {
            fetchMaintenanceOrder();
        } else {
            setError('Invalid maintenanceOrder ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!maintenanceOrder) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">MaintenanceOrder not found</h2>
            </div>
        );
    }

    return (
        <div className="mt-6 w-full">
            <Heading />
            <CarDetails car={maintenanceOrder.vehicle} />
            <MaintenanceOrderForm maintenanceOrder={maintenanceOrder} />
        </div>
    );
};

export default MaintenanceEditPage;
