import { Car } from '@/types/cars.types';
import LicensePlateIcon from '@/components/icons/car/CarNumberIcon';

interface Props {
    car: Car;
}

const CarDetails = ({ car }: Props) => {
    return (
        <div className="mb-8">
            <div className="flex flex-row items-center gap-6">
                <div className="flex-1">
                    <div className="flex flex-row items-center gap-2 justify-between p-2 rounded-lg border border-primery-blue-300">
                        <p className="font-bold text-[22px] text-white-blue-900 leading-[33px] tracking-normal text-start">
                            ح ن ط 7356
                        </p>
                        <LicensePlateIcon />
                        <p className="font-bold text-[22px] text-white-blue-900 leading-[33px] tracking-normal text-start">
                            T N K 7356
                        </p>
                    </div>
                    <div className="flex h-full flex-col gap-1 shadow-[0px_2px_12px_0px_#14142B14] rounded-lg p-2">
                        <div className="flex flex-row justify-between">
                            <div className="inline-block">
                                <img className="h-4 w-4 inline-block me-1" src={car?.brand?.logo} alt="" />
                                <p className="inline-block text-white-blue-900 font-cairo font-medium text-[16px] leading-[20px] tracking-normal text-right">
                                    {car.brand.name} {car.brand_model.name} {car.make_year}
                                </p>
                            </div>
                        </div>
                        <div>
                            <img src={car?.media?.image} className="h-[119px] w-full object-contain" alt="" />
                        </div>
                    </div>
                </div>
                <div className="flex-1">
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            الماركة والموديل
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">
                            <img src={car.brand.logo} alt="" />
                            {car.brand.name} {car.brand_model.name}
                        </p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            نوع ناقل الحركة
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.transmission_type}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            سعة المحرك
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.brand_model.engine_size}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            عداد المسافة الحالي
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.mileage} كيلومتر</p>
                    </div>
                </div>
                <div className="flex-1">
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            اللون
                        </p>
                        <div className="flex flex-row items-center gap-4 p-2 w-full">
                            <span
                                className="w-[30px] h-[30px] rounded-full"
                                style={{ backgroundColor: car.color }}
                            ></span>
                            {car.color}
                        </div>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            نوع السيارة
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.brand_model.type}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            رقم الهيكل
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.vin}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-full">
                            الحالة الحالية
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">
                            <span className="px-3 py-[6px] bg-[#F59E0B] rounded text-base text-white-blue-50 font-medium tracking-normal leading-5">
                                {car.status}
                            </span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CarDetails;
